/* NOTES CSS */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.container-notesList{
  max-width: 960px;
  min-height: 100vh;
  margin: 0 auto 0 auto;
  padding: 0 15px 0 15px;
}

.notes-list{
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));/* fit as many column in a line as possible of 250px */
  
}

::-webkit-scrollbar {
  display: none;
}

.note.new{
  height: 250px;
  background-color: #67d7cc;

}
.note h3,span{
  overflow-wrap: break-word;
  overflow-y: scroll;
}
.note textarea{
  border: none;
  resize: none;
  background-color: transparent;
}
.note textarea:focus{
  outline: none;
}

.save{
  background-color: #e1e1e1;
  border: none;
  border-radius: 15px;
  padding: 5px 10px 5px 10px;
  
}

.save:hover{
  background-color: #ededed;
  cursor: pointer;
}

.note{
  background-color: #fef68a;
  margin-bottom: 10px;
  border-radius: 10px;
  padding: 1rem;
  min-height: 170px;
  max-height: 250px;
  /* box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23); */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  white-space: pre-wrap;
}

.note-footer{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.delete-icon{
  
  cursor:pointer;
}

.search{
  display: flex;
  align-items: center;
  background-color: rgb(233,233,233);
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 1.5em;
}

.search input{
  border: none;
  background-color: rgb(233,233,233);
  min-width: 90%;
  cursor: pointer;
}
.search input:focus{
  outline: none;
}

/* note-list Header */
.note-header{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.notes-icon{
    width: 60px;
    display: flex;
    justify-content: space-between;
}

.copy-icon{
  cursor: pointer;
}
.copy-icon:hover,.delete-icon:hover{
  color: gray;
}

.dark-mode{
  background-color: black;
}
.dark-mode h1{
  color: white;
}